import { Observable, of } from 'rxjs';
import { Injectable } from '@angular/core';
import * as _ from 'lodash';
import { CombinedMachineData } from './models/CombinedMachineData.interface';
import { productionData, oeeData, stateData } from './input-data';

enum MachineStateEnum {
  'warning/orange' = 'st-warn',
  'fatal/red' = 'st-fatal',
  'good/green' = 'st-good'
}

@Injectable({
  providedIn: 'root',
})

export class MachineDataService {
  constructor() { }
  productionData = productionData;
  stateData = stateData;
  oeeData = oeeData;

  combinedData = this.productionData.map(el => {
    const statusValue = this.stateData[el.MACHINE];
    const oeeItem = this.oeeData.find(a => a.MACHINE === el.MACHINE);

    return { ...el, ...oeeItem, status: statusValue };
  });

  dataKeysToLowerCase = this.combinedData.map(data => {
    return _.transform(data, (result, val, key) => {
      result[key.toLowerCase()] = val;
    });
  });
  chartData = [];
  dashboardData = this.dataKeysToLowerCase.map(el => {

    /** Data formatting for chart library */
    let chartDataItem;
    const chartDataSeries = [];
    for (let i = 0; i < 24; i++) {
      chartDataSeries.push({ name: i, value: `${el['h' + i]}` });
      chartDataItem = { name: el.machine, series: chartDataSeries };
    }
    this.chartData.push(chartDataItem);

    /** Data processing for dashboard display */
    const scrap = el.scrap_percentage * el.production / 100;
    el.net = Math.floor(el.production - scrap);
    el.state = MachineStateEnum[el.status];
    return el;
  });

  machineData$: Observable<CombinedMachineData[]> = of(this.dashboardData);
}
